import React from 'react';
import ProjectCard from './ProjectCard';
import PasteType from '../images/project-pt.png';
import Tseudo from '../images/project-tseudo.png';
import Decoder from '../images/project-decoder.png';
import Reservation from '../images/project-reservation.png';
import Library from '../images/project-library.png';
import Pomodoro from '../images/project-pomodoro.png';

export default function Projects() {
    return (
        <>
            <div className='projects-group'>
                <ProjectCard
                    title='pasteType'
                    image={PasteType}
                    description='A small project I put together where you can copy and paste text to test your accuracy and speed. Mainly built to practice lines of code I wanted to memorize.'
                    stack='React Javascript CSS'
                    github='https://github.com/tcouncil/PasteType'
                    live='http://pastetype.councilstudios.com'
                />
                <ProjectCard
                    title='Reservation System'
                    image={Reservation}
                    description='A Restaurant Reservation System that is used to keep track of guest reservations and table assignments.
                    The user can create new reservations and search for standing reservations by mobile phone number.
                    Keep track of where reservations are seated and occupied tables.'
                    stack='React, Express, Node, PostgreSql, KnexJs, Javascript, HTML, CSS'
                    github='https://github.com/tcouncil/thinkful-final-reservation-system'
                    live='https://front-end-tcouncil.vercel.app'
                />
                <ProjectCard
                    title='Decoder Ring'
                    image={Decoder}
                    description='An application that encodes & decodes messages three different ways. I made it for my first capstone project at Thinkful.'
                    stack='Javascript'
                    github='https://github.com/tcouncil/decoder'
                    live='https://tcouncil.github.io/decoder/index.html'
                />
            </div>
            <div className='projects-group'>
                <ProjectCard
                    title='Pomodoro Timer'
                    image={Pomodoro}
                    description='A simple Pomodoro Timer for setting Focus Times and Break Times'
                    stack='React Javascript'
                    github='https://github.com/tcouncil/PomodoroTimer'
                    live='https://tcouncil.github.io/PomodoroTimer/'
                />
                <ProjectCard
                    title='Tseudo'
                    image={Tseudo}
                    description='A casual ongoing project that is a generated platformer. Play your way through the darkness and gain insight to "enhance" your perception. By gaining orbs you gain speed and sight.'
                    stack='Phaser 3 Javascript'
                    github='https://github.com/tcouncil/tseudo'
                    live='http://tseudo.councilstudios.com'
                />
                <ProjectCard
                    title='Project Local Library'
                    image={Library}
                    description='An application which displays information and statistics from a library.'
                    stack='Javascript HTML CSS'
                    github='https://github.com/tcouncil/project-local-library'
                    live='https://project-local-library-khaki.vercel.app'
                />
            </div>
            <p className='about'>More Projects Coming Soon!</p>
        </>
    )
}